import React from 'react';
import { IconButton, ListItemIcon, ListItemText, MenuItem, SvgIcon, Typography } from '@mui/material';
import { copyTextToClipboard } from '../../../../../utils/helpers';
import { useTranslationContext } from '../../../../Providers/TranslationProvider';
import userStore from '../../../../../core/stores/userStore';
import tabStore, { setTabMetaCache } from '../../../../../core/stores/tabStore';
import { UnfoldLess } from '@mui/icons-material';
import { UnfoldMore } from '@mui/icons-material';
import PremiumTipsIcon from '../../../../component/PremiumTipsIcon';


const TabHistoryExpand = ({ tabId, handleClose }) => {
    const { t } = useTranslationContext();
    const { tabHistoryEnable, tabHistoryExpand } = userStore((state) => ({
        tabHistoryEnable: state.settings.tabHistoryEnable,
        tabHistoryExpand: state.settings.tabHistoryExpand,
    }));

    const { urlHistoriesLength, expandUrlHistory, setTabConfig } = tabStore((state) => {
        const tab = state.tabs.find((t) => t.id === tabId) || {};
        return {
            urlHistoriesLength: tab.urlHistories?.length,
            expandUrlHistory: tab.expandUrlHistory,
            setTabConfig: state.setTabConfig
        };
    });

    const realExpand = tabHistoryEnable && (expandUrlHistory === undefined ? tabHistoryExpand : expandUrlHistory);

    const handleClick = async (event) => {
        event.stopPropagation();
        const newStatus = !realExpand;
        await setTabConfig(tabId, { expandUrlHistory: newStatus });
        await setTabMetaCache(tabId, 'expandUrlHistory', newStatus);
        if (typeof handleClose === 'function') {
            handleClose();
        }

    };
    if (!tabHistoryEnable || urlHistoriesLength < 2) {
        return null
    }

    return (
        <MenuItem dense onClick={handleClick}>
            <ListItemIcon>
                <IconButton sx={{ padding: "3px", color: "inherit" }}>
                    {realExpand ? <UnfoldLess fontSize="small" /> : <UnfoldMore fontSize="small" />}
                </IconButton>
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{realExpand ? t('collapseTabHistories') : t('expandTabHistories')}</Typography>
            </ListItemText>
        </MenuItem>
    );
};

export default TabHistoryExpand;
